<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "fireSuspendApplyComponent",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      form: {
        WorkDate: [this.formatDate(new Date()), this.formatDate(new Date())],
        WorkDate1: "",
        WorkDate2: "",
        Location_Id: "LOCATION_ID",
        Project_No: "",
        InsuranceNo: "",
        NotifyStatus: "ALL",
        Status: "ALL",
      },
      rules: {
        WorkDate: [{ required: true, message: "必填", trigger: "change" }],
      },
      fireSuspendApplyTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "insurance_No",
          label: "中斷申請單號",
          width: "140",
        },
        {
          id: "project_No",
          label: "工程單單號",
          width: "140",
        },
        {
          id: "factory",
          label: "廠區/廠商名稱",
          width: "200",
        },
        {
          id: "department",
          label: "發包工程師/部門",
          width: "140",
        },
        {
          id: "dateRange",
          label: "中斷期間",
          width: "200",
        },
        {
          id: "status",
          label: "簽核狀態",
          width: "140",
        },
      ],
      selectedEnter: [],
      selectedRetired: [],
      facs: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    formatWorkDate(form) {
      this.form.WorkDate1 = form.WorkDate[0];
      this.form.WorkDate2 = form.WorkDate[1];
    },
    stopEnterHandler(evt, item) {
      if (evt) {
        this.selectedEnter.push(item.insurance_No);
        item.enterCheck = true;
      } else {
        this.selectedEnter = this.selectedEnter.filter(
          (e) => e !== item.insurance_No
        );
        item.enterCheck = false;
      }
    },
    overRetiredHandler(evt, item) {
      if (evt) {
        this.selectedRetired.push(item.insurance_No);
        item.retiredCheck = true;
      } else {
        this.selectedRetired = this.selectedRetired.filter(
          (e) => e !== item.insurance_No
        );
        item.retiredCheck = false;
      }
    },
    async confirmEnter() {
      try {
        const obj = {
          Enter: this.selectedEnter,
          Retired: [],
        };
        const res = await this.$store.dispatch("OSHUpdInsuranceNotify", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "消防中斷修改成功",
          });
          this.getInsuranceList(this.form);
          this.$refs.insuranceTable.$refs.multipleTable.clearSelection();
          this.selectedEnter = [];
          this.selectedRetired = [];
          this.$store.state.currentPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async confirmRetired() {
      try {
        const obj = {
          Enter: [],
          Retired: this.selectedRetired,
        };
        const res = await this.$store.dispatch("OSHUpdInsuranceNotify", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "消防終止修改成功",
          });
          this.getInsuranceList(this.form);
          this.$refs.insuranceTable.$refs.multipleTable.clearSelection();
          this.selectedEnter = [];
          this.selectedRetired = [];
          this.$store.state.currentPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    openWorkOrderDetails(dailyNo) {
      window.open(
        `/#/engineeringDataManagementViewDetail/${encodeURIComponent(dailyNo)}`
      );
    },
    async exportExcel(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.formatWorkDate(this.form);
          try {
            const res = await this.$store.dispatch("commonExportExcel", {
              obj: this.form,
              router_Name: config.getOSH,
              action_Name: "ExportInsurance",
            });
            this.downloadFile(res, "消防中斷申請", "xlsx");
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    searchForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.formatWorkDate(this.form);
          this.getInsuranceList(this.form);
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async getInsuranceList(formData) {
      try {
        const params = this.getURLNewParams(formData);
        const res = await this.$store.dispatch("OSHQryInsuranceList", params);
        const data = res.data.data;
        this.tableData = JSON.parse(JSON.stringify(data));
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.factory = `${e.location_Name}/${e.s_Name}`;
          e.department = `${e.emp_Name}(${e.emp_No})/${e.dept_Name}`;
          e.dateRange = `${e.begin_Work_Date} ~ ${e.end_Work_Date}`;
          e.status = this.convertStatus(e.status);
        });
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    async getInsuranceCondition() {
      try {
        const res = await this.$store.dispatch("OSHQryInsuranceCondition");
        const data = res.data.data;
        this.facs = data.facs;
      } catch (err) {
        console.log(err);
      }
    },
  },
  mounted() {
    this.getInsuranceCondition();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./fireSuspendApply.html"></template>
